<template>
  <div  class="h-100">
    <Loader v-if="game.loading"/>
    <div v-else class="game-container d-flex flex-column align-items-center flex-grow-1 mb-2 h-100"> 
      <GameHeader />
      <div class="row align-self-end d-flex justify-content-center w-100 h-100 px-0 mx-1">
        <div class="d-none d-lg-block d-flex col-0 col-lg-2"></div>
        <Game />
        <Hints />        
      </div><!-- row ends here -->
    </div><!--game-container ends here -->
  </div><!-- root div ends here -->
</template>



<script setup>
  import {mapMutations, mapGetters,  mapActions} from '@/libs/vuex'
  import {reactive, onMounted, onBeforeUnmount} from 'vue'

  //Components
  import Game from './game-layout.vue'
  import Loader from '@/components/misc/loading.vue'
  import Hints from '@/components/cards/hints-card.vue'
  import GameHeader from '@/components/misc/game-header.vue'

  const {startGame, toggleModal,  pushCurrentScore,  toggleSideNav} = mapMutations()
  const {saveScore} = mapActions()

  const game = reactive({
    loading: true,               //ðŸ‘ˆ determines if the loading screen currently active
    loadTime: 4500,              //ðŸ‘ˆ the amount of time in milliseconds the loading screen is active for
  })
  

  onMounted(async()=> {
    logOutInnerText('Quit Game')
    game.loading = await load(game.loadTime)
    toggleModal('rules')
  })

  onBeforeUnmount(async()=>{
    startGame(false)
    pushCurrentScore()
    await saveScore()
    logOutInnerText('logout')
    toggleSideNav(true) 
  })


  const logOutInnerText = innerText =>{
    const btn = document.getElementById('logOutBtn')
    btn.innerText = innerText
  }


  const load = time => new Promise((resolve, _reject) =>setTimeout(() => resolve(!game.loading), time))

</script>



<style scoped>
  .game-container{
    width: 100%;
    height: 100%;
    background-image: url('@/assets/img/jungle-background.webp');
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }

  

</style>