<template>
  <div class="d-flex justify-content-between align-items-center bg-white shadow-sm p-2 p-md-3  my-3 w-75 w-lg-60">
    <p class="mb-0">Level: <span class="text-success fw-bold">{{getCrushState('currentLvL')}}</span></p>
    <p class="mb-0 text-muted">{{getCrushState('currentScore')}} / <span class="text-success">{{game.goal}} </span></p>
    <p class="mb-0">Target: <span class="text-danger fw-bold">{{game.target}}</span></p>

  </div>
</template>


<script setup> 
  import {mapGetters} from '@/libs/vuex'
  import { onMounted,  reactive, watch} from 'vue'

  const {getCrushState,  getCrushLvlState,  getCrushCurrentLvL} = mapGetters()
  
  const game = reactive({
    goal:null, 
    target: null,
  })


  onMounted(()=>init())
  watch(() => getCrushCurrentLvL, () => init(),{ deep: true })

  const init= () =>{
    const lvl = getCrushState.value('currentLvL')
    game.goal  = getCrushLvlState.value(lvl-1).goal
    game.target  =  getCrushLvlState.value(lvl-1).target
  }
</script>