<template>
  <div  class="col-12 col-lg justify-content-center  justify-content-lg-end w-100 h-100 ">
    <Grid />

    <div id="progress-bar-container" class="progress mx-2 mx-sm-auto my-3 ">
      <div class="progress-bar progress-bar-striped progress-bar-animated bg-warning " role="progressbar" style="width: 100%"  aria-valuemin="0" aria-valuemax="100"></div>
    </div>  
          
    <div class="d-flex align-items-center justify-content-center pt-3">
      <button :disabled="getPauseStatus"  @click="[toggleModal('restart'), pauseGame(), toggleCoins()]" type="button" class="btn btn-danger mx-1 py-0 px-md-3 rounded-1 btn-highlight-none">Restart</button>
      <button  @click="[pauseGame(), toggleCoins()]" id="pauseBtn" type="button" class="btn btn-warning text-white mx-1 py-0 px-md-3 btn-highlight-none">Pause</button>
      <button :disabled="getPauseStatus" @click="showHint" type="button" class=" d-none d-lg-block btn btn-success mx-1 py-0 px-md-3 btn-highlight-none">Hints</button>
      <button :disabled="getPauseStatus" @click="[toggleModal('scores'), pauseGame(), toggleCoins()]" type="button" class="btn btn-light mx-1 py-0 px-md-3 btn-highlight-none">Scores</button>
    </div>
  </div>
</template>

<script setup>
  import { mapMutations, mapGetters} from '@/libs/vuex'
  import Grid from '@/components/misc/grid.vue'
  import {toggleCoins} from '@/views/partials/math-crush/game-helpers'

  const {toggleModal, pauseGame} = mapMutations() 
  const {getPauseStatus} = mapGetters()


  const flip = () => {
    const target = document.getElementsByClassName('hint')
    anime({
      targets: target,
      scale: [{value:1}, {value:1.3},{value:1, delay: 250} ],
      rotateY: {value: "+=180", delay: 200},
      easing: "easeInOutSine",
      duration: 400,
    })
  }

  const showHint = () =>{
    flip()
    setTimeout(()=> flip(), 10000)
  }







</script>




<style scoped>

.pulse{
    box-shadow: 0 0 0 0 rgba(220, 53, 69, 1);
    animation: pulse-red 2s infinite;
  }
    
  @keyframes pulse-red {
    0% {box-shadow: 0 0 0 0 rgba(255, 53, 69, 0.7);}
    70% {box-shadow: 0 0 0 10px rgba(255, 53, 69, 0);}
    100% {box-shadow: 0 0 0 0 rgba(255, 53, 69, 0);}
  }


</style>