<template>
  <div class="d-flex justify-content-center w-100 h-75">
    <div id="myGrid" class="d-flex flex-wrap justify-content-center justify-content-lg-start w-100 h-100 rounded rounded-6 transparent-dark">
      <div v-for="(tile, index) in getCrushState('gridSize') * getCrushState('gridSize')" :key="index" class="tile-container d-flex justify-content-center align-items-center">
        <div :id="index" @click="onClick" v-touch:swipe="onSwipe">
          <div class="w-100 h-100"></div>
        </div>
      </div>  
    </div>
  </div>
</template>




<script setup>
  import {mapGetters, mapMutations} from '@/libs/vuex'
  import {onMounted,  watch,  onBeforeUnmount} from 'vue'
  import 
  {
    onClick, onSwipe,
    createTiles, createGrid,
    startGame, checkScores, createNewGame, clearTimer 
  } from '@/views/partials/math-crush/game-helpers'



  const{resetGrid} = mapMutations()
  const { getCrushState, getPauseStatus, getCurrentScore, getCrushCurrentLvL} = mapGetters()


  onMounted(()=>{
    createGrid('tile-container')
    createTiles(getCrushState.value('grid'))
  })

  onBeforeUnmount(()=>{
    clearTimer()
    resetGrid()
  })


  watch(()=> getCurrentScore, () => checkScores(), {deep: true})
  watch(() => getCrushCurrentLvL, () => createNewGame(),{ deep: true })
  watch(() => getPauseStatus, () => startGame(getPauseStatus.value), { deep: true })
</script>


<style>
  .tile-container{
    width: 12.5%;
    height: 12.5%;
  }

</style>